/* Popup container */
.popup {
  position: relative;
  display: inline-block;
  cursor: pointer;
  width: 100%;
  height: 100%;
}
/* The actual popup (appears on top) */
.popup .popuptext {
  min-width: 250px;
  background-color: #555;
  color: #000;
  text-align: center;
  border-radius: 6px;
  padding: 8px 0;
  position: absolute;
  z-index: 1;
  right: 10%;
  left: 10%;
  background-color: rgba(123,200,10,0.8);
}
/* Add animation (fade in the popup) */
@-webkit-keyframes fadeIn {
from {opacity: 0;
}
to {opacity: 1;
}
}
@keyframes fadeIn {
from {opacity: 0;
}
to {opacity:1 ;
}
}
